  overflow-x: hidden;
} */

body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.navbar {
  height: 100px;
  width: 100%;
  background-color: #fff;
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 24px;
  margin-bottom: 50px;
  box-shadow: 0 1px 7px;
}
/* max-width: 450px */
@media screen and (max-width: 450px) {
  .navbar {
    width: 105%;
  }
  .div_for_content {
    width: 95% !important;
  }
  .contacts {
    right: 15px !important;
    font-size: 16px !important;
  }
  #custom_card {
    width: 80% !important;
    margin: 20px auto;
  }
  .custom_main {
    margin: 0 auto;
  }
  #custom_big_picture {
    position: fixed;
    left: 0;
    top: 0;
    width: 100%;
    height: 107vh !important;
    background-color: rgba(0, 0, 0, 0.849);
    z-index: 6;
    display: flex;
    justify-content: center;
    align-items: center;
  }
  #custom_big_picture img {
    width: 90%;
    margin-left: 5px;
  }
  #custom_div_image {
    display: flex !important;
    justify-content: center;
    align-items: center;
  }
  .pagesNumbersBlock span {
    margin: 0 2px !important;
  }
  .pagesNumbersBlock button {
    margin: 0 4px !important;
  }
}
/*  */
/* min-width: 451px | max-width: 776px  */
@media screen and (max-width: 776px) and (min-width: 451px) {
  .navbar {
    width: 105%;
  }
  .div_for_content {
    width: 80% !important;
  }
  #custom_card {
    width: 65% !important;
    margin: 20px auto;
    position: relative;
  }
  .custom_main {
    margin: 0 auto;
    max-width: 100%;
  }
  #custom_big_picture {
    position: fixed;
    left: 0;
    top: 0;
    width: 100%;
    height: 107vh !important;
    background-color: rgba(0, 0, 0, 0.849);
    z-index: 6;
    display: flex;
    justify-content: center;
    align-items: center;
  }
  #custom_big_picture img {
    width: 80%;
    margin: 0 auto;
  }
  #custom_div_image {
    display: flex;
    justify-content: center;
    align-items: center;
  }
  .pagesNumbersBlock span {
    margin: 0 2px !important;
  }
  .pagesNumbersBlock button {
    margin: 0 4px !important;
  }
}
/*  */
/* min-width: 777px */
@media screen and (min-width: 777px) {
  #custom_big_picture img {
    height: 80vh;
  }
}
/*  */

@media screen and (min-width: 1000px) and (max-width: 1500px) {
  .div_for_content {
    width: 75% !important;
  }
}
.preloader {
  position: fixed;
  height: 100vh;
  width: 100%;
  background-color: #fff;
  z-index: 10;
  display: flex;
  justify-content: center;
  align-items: center;
}

#custom_card:hover {
  transition: 0.2s;
  box-shadow: 0 1px 7px;
}
#custom_card img {
  cursor: pointer;
}

#custom_big_picture {
  position: fixed;
  left: 0;
  top: 0;
  width: 100%;
  height: 100vh;
  background-color: rgba(0, 0, 0, 0.849);
  z-index: 6;
  display: flex;
  justify-content: center;
  align-items: center;
}
.modal_container {
  z-index: 7;
  position: fixed;
  height: 105vh;
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: rgba(0, 0, 0, 0.836);
}
.custom_modal {
  position: fixed;
  z-index: 8;
  width: 300px;
  height: 400px;
  border-radius: 10px;
  background-color: #fff;
}

#edit {
  z-index: 3;
  margin: 0 auto;
}
.edit_window {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  padding-top: 8px;
  padding-bottom: 8px;
}
.selectedPage {
  background-color: #e9ebee;
  font-weight: bold;
}
.pagesNumbersBlock {
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  height: 40px;
  border-bottom: 1px solid #cdced3;
}
.pagesNumbersBlock span {
  margin: 0 10px;
  border: 1px solid #cdced3;
  padding: 5px 10px;
  border-radius: 50%;
  color: #797575;
  cursor: pointer;
  user-select: none;
}
.pagesNumbersBlock span:hover {
  background-color: #f4f5f8;
}
.pagesNumbersBlock button {
  overflow: hidden;
  outline: none;
  user-select: none;
  font-weight: bold;
  margin: 0 10px;
  border: 1px solid #cdced3;
  padding: 5px 10px;
  border-radius: 50%;
  color: #797575;
  cursor: pointer;
  user-select: none;
}
.pagesNumbersBlock button:hover {
  background-color: #f4f5f8;
}
.password_button_div {
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  margin-top: 30px;
  margin-bottom: 30px;
}
.password_button_div button {
  background-color: rgb(243, 243, 243);
  border: none;
  outline: none;
}
.password_modal {
  height: 200px !important;
}
.warning_editor_mode {
  color: red;
  text-align: center;
  margin-top: 8px !important;
  margin-bottom: 5px;
}
.warning_incorrect_password {
  color: red;
  text-align: center;
  margin-top: 6px;
  margin-bottom: 6px;
}
.add_button {
  position: absolute;
  background-color: #fff;
  color: rgb(63, 62, 62);
  border: none;
  outline: none;
  font-size: 16px;
  top: 45px;
  border-radius: 5px;
  text-decoration: none;
  box-shadow: 0px 1px 5px;
  left: 3px;
}
.edit_button {
  color: rgb(63, 62, 62);
  position: absolute;
  top: 88px;
  left: 3px;
  background-color: #fff;
  border: none;
  outline: none;
  font-size: 16px;
  border-radius: 5px;
  text-decoration: none;
  box-shadow: 0px 1px 5px;
}

.contacts {
  font-size: 18px;
  position: absolute;
  right: 0;
  cursor: pointer;
}

.div_for_content {
  width: 55%;
  margin: 0 auto;
  display: flex;
  justify-content: center;
  align-items: center;
  position: relative;
}
.contacts_modal {
  display: flex;
  justify-content: space-around;
  flex-direction: column;
  align-items: center;
}
.contacts_icons {
  font-size: 20px !important;
}
.add_modal {
  display: flex;
  justify-content: space-around;
  align-items: center;
  flex-direction: column;
}
.instruction_container {
  margin-bottom: 20px;
  font-size: 20px;
}
